
import { defineComponent, inject } from "vue";
import RecursionItem from "@/views/RecursionItem.vue";
interface IChild {
  name: string | number;
  children: Array<IChild>;
}

const __default__ = defineComponent({
  components: {
    RecursionItem,
  },
  setup() {
    const color = inject('$color');
    return { color };
  },
  data() {
    return {
      treeData: {
        name: "My Tree",
        children: [
          { name: "hello" },
          { name: "wat" },
          {
            name: "child folder",
            children: [
              {
                name: "child folder",
                children: [{ name: "hello" }, { name: "wat" }],
              },
              { name: "hello" },
              { name: "wat" },
              {
                name: "child folder",
                children: [{ name: "hello" }, { name: "wat" }],
              },
            ],
          },
        ],
      },
    };
  },
  methods: {
    makeFolder(item: IChild) {
      item.children = [];
      this.addItem(item);
    },
    addItem(item: IChild) {
      item.children.push({
        name: "new stuff",
        children: [],
      });
    },
  },
});

import { useCssVars as _useCssVars } from 'vue'
const __injectCSSVars__ = () => {
_useCssVars(_ctx => ({
  "26f2acd0-color": (_ctx.color)
}))}
const __setup__ = __default__.setup
__default__.setup = __setup__
  ? (props, ctx) => { __injectCSSVars__();return __setup__(props, ctx) }
  : __injectCSSVars__

export default __default__